import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Http, Response } from '@angular/http';
//import 'rxjs/Rx';

import { ToastrService, WebConfigService } from '../../shared/index'; 
//import { BaseErrorHandlerComponent } from '../../shared/components/base-error-handler/base-error-handler.component';
import { Tool } from '../models/tool';

@Injectable()
export class ToolListService  {

    constructor(private http: Http, private toastrService: ToastrService) {
        //super(toastrService);
        //console.log('Tools Service Initialized...');
    }
    
    getTools() {

        let data = this.http.get("/api/Tool/GetTools")
                    .map(this.extractData)
                    .catch(this.handleError);
        return data;
    }

    private extractData(res: Response) {
        const body = res.json() as Tool[];
        return body || null;
    }

    public handleError(error: any) {
        // In a real world app, we might use a remote logging infrastructure
        // We'd also dig deeper into the error to get a better message

        //console.error(error); // log to console instead
        console.log(error);
        // this.toastrSvc.
        return Observable.throw(error.statusText);
    }
}



