import { Component, ElementRef, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { BlockUI, NgBlockUI } from 'ng-block-ui';

import { ToastrService, WebConfigService } from '../../../shared/index';
import { ToolListService } from '../../services/tool-list.service'; 
import { User } from '../../../shared/models/user';

@Component({
    selector: "tool-list",
    templateUrl: "./tool-list.component.html",
    styleUrls: ["./tool-list.component.css"],
    //providers: [ToolListService]
})
export class ToolListComponent implements OnInit{

    @BlockUI() blockUI: NgBlockUI;

    public tools: any = [];
    public user: any;
    public webconfig: any;
    
    constructor(private router: Router, private route: ActivatedRoute,
        private toolListSvc: ToolListService,  
        private webConfigSvc: WebConfigService )
    {
        this.user = this.route.snapshot.data['currentUser'].model;
    }

    ngOnInit() {

        this.blockUI.start('Loading...');

        this.webConfigSvc.getWebConfig().then((resp: any) => {
            this.webconfig = resp;
        }).catch(error => {
            console.log("error message: " + error.message);
            console.log("error stack: " + error.stack);
            });

        this.tools = this.getAllTools();
    }

    getAllTools() {

        return this.toolListSvc.getTools().subscribe(data => {
            this.tools = data;

            for (var i = 0; i < this.tools.length; i++) {
                if (this.tools[i].accessUrl != null) {
                    this.tools[i].clickable = true;

                    if (this.tools[i].toolId == 120) {
                        this.tools[i].accessUrl = this.webconfig.routeConfig.lcstURL +
                            "&userId=" + this.user.userId + "&firstName=" + this.user.firstName +
                            "&lastName=" + this.user.lastName + "&email=" + this.user.email;
                    }
                }
                else {
                    this.tools[i].downloadable = true;
                }
            }

            this.blockUI.stop();
        }, err => {
                console.log(err);
                this.blockUI.stop();
        });       
    }

    convertToSafeHtml(description:string) {
        return 
    }

    ngOnDestroy() {
        
    }
}
    

     


