import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ToolListComponent, UnitaryMatchupComponent, LCSplitMatchupComponent } from './index';
import { CurrentUserResolver } from '../shared/index';

const toolsRoutes: Routes = [
    {
        path: 'tool-list', component: ToolListComponent,
        resolve: {
            currentUser: CurrentUserResolver
        }
    },
    {
        path: 'unitaryMatchup', component: UnitaryMatchupComponent,
        resolve: {
            currentUser: CurrentUserResolver
        }
    },
    {
        path: 'lcSplitMatchup', component: LCSplitMatchupComponent,
        resolve: {
            currentUser: CurrentUserResolver
        }
    }
];

@NgModule({
    imports: [RouterModule.forChild(toolsRoutes)],
    exports: [RouterModule],
})
export class ToolsRoutingModule { }


